@import url(//fonts.googleapis.com/earlyaccess/kopubbatang.css);

* {
  font-family: 'KoPub Batang', serif;
  box-sizing: border-box;
  transition: all .3s;
  word-break: keep-all;
}

*::selection {
  background-color: rgb(248, 248, 248);
  color: rgb(192, 48, 48);
}

body, p {
  /* background: rgba(160, 48, 48); */
  /* background: #222; */
  margin: 0;
  padding: 0;
  line-height: 1;
  background: rgb(248, 248, 248);
}
p { background: transparent; }

a { text-decoration: none; }
button {
  border: none;
  cursor: pointer;
  background-color: transparent
}


/* App */
.App {
  display: flex;
  flex-flow: row wrap;
  width: 800px;
  min-height: 100vh;
  justify-content: center;
  /* background: #222; */
  font-size: 1.1rem;
  text-align: center;
  margin: 0 auto;
  /* box-shadow: 30px 0 30px #922, -30px 0 30px #922; */
  /* border: 5px solid rgba(176, 48, 48); */
  border-width: 0 4px;
  box-sizing: border-box;
  position: relative;
}

/* dark mode add */
.darkFont { color: #edd !important; }
.darkbg { background: #222 !important; }
.darkInput { 
  border-bottom: 1px solid #edd !important; 
  color: #edd !important;
}

.modal {
  width: 500px;
  height: 500px;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  background: rgb(248, 248, 248);
  border-radius: 30px;
  position: relative;
  box-shadow: 10px 0 10px #ddd, -10px 0 10px #ddd;
  padding: 30px;
  font-size: 1.1rem;
}

.modal > .hint {
  text-align: center;
}

.modalCloseBtn {
  position: absolute;
  top: 30px;
  right: 30px;
  color: red;
  font-size: 3rem;
  height: 50px;
  width: 50px;
  font-weight: 900;
}

/* ReactModal Overlay */
.ReactModal__Overlay {
  opacity: 0;
  transition: opacity 250ms ease-in-out;
}
.ReactModal__Overlay--after-open{
  opacity: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.ReactModal__Overlay--before-close{
  opacity: 0;
}

.header {  
  background: rgba(160, 48, 48);
  color: #fee;
  height: 90px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
  font-size: 1.8rem;
  font-weight: bold;
  position: absolute;
  top: 0;
}
.dummy {
  width: 55px;
  height: 55px;
}
.bookmark {
  width: 55px;
  height: 55px;
  border-radius: 20px;
}

.solveRiddle-enter {
  background: rgb(192, 48, 48);
  opacity: 1;
}
.solveRiddle-enter-active {
  opacity: 1;
  transition: all 300ms ease-in-out;
}
.solveRiddle-exit {
  opacity: 1;
  background: rgb(192, 48, 48);
}
.solveRiddle-exit-active {
  opacity: 0;
  transition: all 300ms ease-in-out;
}

.content {
  display: flex;
  flex-flow: column nowrap;
  /* justify-content: center; */
  align-items: center;
  text-align: left;
  margin-top: 90px;
  padding: 40px;
  width: 100%;
  overflow: auto;
  /* animation-name: fade-in;
  animation-duration: .7s;
  animation-fill-mode: backwards; */
}

.fadeIn { animation: fade-in .5s backwards; }
.fadeOut { animation: fade-out .5s backwards; }

.content > .riddleP, .hint {
  margin: .5rem 0;
  line-height: 1.5;
  color: #200;
}
.startBtn, .submitAnswer {
  height: 55px;
  width: 55px;
}
.startBtn > img, .bookmark > img, .submitAnswer > img {
  height: 50px;
  width: 50px;
}

.footer {
  margin-bottom: 20px;
  height: 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.notice {
  color: rgb(192, 48, 48);
  font-weight: 700;
  min-height: 20px;
}

.answer {
  border: none;
  border-bottom: 1px solid #200;
  background: transparent;
  padding: .5em;
  font-size: 1rem;
  /* text-align: center; */
  margin-right: .5rem;
  color: #200;
}
.answerContainer {
  display: flex;
  align-items: center;
}

@keyframes fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fade-out {
  from { opacity: 1; }
  to { opacity: 0; }
}

@media (prefers-reduced-motion: no-preference) {
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}